<template>
	<!-- –≤–µ—Ä—Å—Ç–∫–∞ –¥–ª—è —Ç–µ—Å—Ç–∞ -->
		<div class="main">
			<div>
				1. –î–ª–∏–Ω–∞ –ø–∞—Ä–æ–ª—è –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –Ω–µ –º–µ–Ω–µ–µ 8 –∏ –Ω–µ –±–æ–ª–µ–µ 64 —Å–∏–º–≤–æ–ª–æ–≤. <br>
				2. –ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å –∏–∑ –±—É–∫–≤ –ª–∞—Ç–∏–Ω—Å–∫–æ–≥–æ –∞–ª—Ñ–∞–≤–∏—Ç–∞ (A-z), –∞—Ä–∞–±—Å–∫–∏—Ö —Ü–∏—Ñ—Ä (0-9) –∏ —Å–ø–µ—Ü–∏–∞–ª—å–Ω—ã—Ö —Å–∏–º–≤–æ–ª–æ–≤: ( . , : ; ? ! * + % - < > @ [ ] { } ( ) / \ _ {} $ # ).<br>
				3. –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –æ–¥–Ω–∞ –±—É–∫–≤–∞ –≤–µ—Ä—Ö–Ω–µ–≥–æ —Ä–µ–≥–∏—Å—Ç—Ä–∞: –ê, –æ–¥–Ω–∞ –±—É–∫–≤–∞ –Ω–∏–∂–Ω–µ–≥–æ —Ä–µ–≥–∏—Å—Ç—Ä–∞: –∞, —Å–ø–µ—Ü–∏–∞–ª—å–Ω—ã–π —Å–∏–º–≤–æ–ª –∏ –∞—Ä–∞–±—Å–∫–∞—è —Ü–∏—Ñ—Ä–∞.<br>
			</div>
			<input type="text" class="pass_block pass" maxlength="64" placeholder="–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å" v-model="pass">
			<input type="text" class="pass_block pass_confirm" maxlength="64" placeholder="–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å" v-model="pass–°onfirm">
			<button v-on:click="verificationPassword()">–ø—Ä–æ–≤–µ—Ä–∏—Ç—å</button>
			<div class="info">{{info_message}}</div>
		</div>
</template>

<script>
	import sendAjax from '../utils/ajax';

	export default {
		name: 'login',
		data () {
			return {
				pass: '',
				pass–°onfirm: '',
				info_message:'',
				errors: {
					err_format: '–Ω–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –ø–∞—Ä–æ–ª—è', 
					err_confirm: '–ø–∞—Ä–æ–ª—å –Ω–µ —Å–æ–≤–ø–∞–¥–∞–µ—Ç',
					err_emptyField: '–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è',
					good: '–í—Å–µ –æ–∫ --> –¥–∞–ª—å—à–µ —Ä–µ–¥–∏—Ä–µ–∫—Ç –Ω–∞ –∫–∞–±–∏–Ω–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è'
				}
			}
		},			
	  	methods: {
	  		verificationPassword: function () {
	  			let passStatus = /(?=.*[0-9])(?=.*[.,:;?!*+%\-<>@[\]{}()/\\_$#])(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z\.,:;\?!\*\+%-<>@\[\]\{\}/\\_\$#]{8,}/g.test(this.pass);
					// (?=.*[0-9]) - —Å—Ç—Ä–æ–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç —Ö–æ—Ç—è –±—ã –æ–¥–Ω–æ —á–∏—Å–ª–æ;
					// (?=.*[.,:;?!*+%\-<>@[\]{}()/\\_$#]) - —Å—Ç—Ä–æ–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç —Ö–æ—Ç—è –±—ã –æ–¥–∏–Ω —Å–ø–µ—Ü—Å–∏–º–≤–æ–ª –∏–∑ —Å–ø–∏—Å–∫–∞ .,:;?!*+%-<>@[]()/\_{}$#;
					// (?=.*[a-z]) - —Å—Ç—Ä–æ–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É –ª–∞—Ç–∏–Ω—Å–∫—É—é –±—É–∫–≤—É –≤ –Ω–∏–∂–Ω–µ–º —Ä–µ–≥–∏—Å—Ç—Ä–µ;
					// (?=.*[A-Z]) - —Å—Ç—Ä–æ–∫–∞ —Å–æ–¥–µ—Ä–∂–∏—Ç —Ö–æ—Ç—è –±—ã –æ–¥–Ω—É –ª–∞—Ç–∏–Ω—Å–∫—É—é –±—É–∫–≤—É –≤ –≤–µ—Ä—Ö–Ω–µ–º —Ä–µ–≥–∏—Å—Ç—Ä–µ;
					// [0-9a-zA-Z!@#$%^&*]{8,} - —Å—Ç—Ä–æ–∫–∞ —Å–æ—Å—Ç–æ–∏—Ç –Ω–µ –º–µ–Ω–µ–µ, —á–µ–º –∏–∑ 8 –≤—ã—à–µ—É–ø–æ–º—è–Ω—É—Ç—ã—Ö —Å–∏–º–≤–æ–ª–æ–≤.

					if (this.pass && this.pass–°onfirm) {
						if (!passStatus) {
							this.info_message = this.errors.err_format
						} else {
							if (this.pass == this.pass–°onfirm) {	
								this.info_message = this.errors.good
								this.sendPassword();	//—Ç—É—Ç –º—ã –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º –ø–∞—Ä–æ–ª—å –Ω–∞ –±—ç–∫
							} else {
								this.info_message = this.errors.err_confirm
							}	
						}						
					} else {
						this.info_message = this.errors.err_emptyField
					}
	  		},
			sendPassword: function () {
		  		let PasswordJson = JSON.stringify(this.pass);

		  		sendAjax('https://–∞–¥—Ä–µ—Å_—Å–µ—Ä–≤–µ—Ä–∞.com/', 'post', PasswordJson)
					.then((response) => {
						//—Å–µ—Ä–≤–µ—Ä –ø–æ–ª—É—á–∏–ª –ø–∞—Ä–æ–ª—å, –ø–µ—Ä–µ—Ö–æ–¥–∏–º –¥–∞–ª—å—à–µ - –∫–∞–±–∏–Ω–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è?
				    },
				    (response) => {
				    	//–∑–¥–µ—Å—å –≤—ã–ø–æ–ª–Ω—è–µ–º –¥–µ–π—Å—Ç–≤–∏—è –ø—Ä–∏ –Ω–µ—É–¥–∞—á–Ω–æ–º –∑–∞–ø—Ä–æ—Å–µ –∫ —Å–µ—Ä–≤–µ—Ä—É   
				    })
			} 	  			  	
		}  			
	}
</script>

<style scoped>
	.main {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	.pass_block {
		width: 600px;
		height: 30px;
		margin: 20px;
	}
	.info {
		margin-top: 30px;
		color: red;
		font-size: 25px;
	}
</style>